import { NextSeo } from 'next-seo';
import { Button, Card, CardContent, Stack, Typography } from '@mui/material';
import React, { useContext, useEffect, useState } from 'react';
import { AppContext } from '../../../components/common/context/AppProvider';
import Tabber from '../../../components/common/Tabber';
import GuildMembers from '../../../components/account/Guild/GuildMembers';
import GuildBonuses from '../../../components/account/Guild/GuildBonuses';
import { numberWithCommas, prefix, tryToParse } from '../../../utility/helpers';
import ProgressBar from '../../../components/common/ProgressBar';
import { format } from 'date-fns';
import Box from '@mui/material/Box';
import Popper from '@components/common/Popper';
const Guild = () => {
  const {
    state
  } = useContext(AppContext);
  const {
    guild
  } = state?.account;
  const [dataTimestamp, setDataTimestamp] = useState([]);
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [error, setError] = useState('');
  useEffect(() => {
    const lsData = tryToParse(localStorage.getItem('guild'));
    if (lsData) {
      setDataTimestamp(Array.isArray(lsData) ? lsData : [lsData]);
    }
  }, []);
  const saveToLS = () => {
    const timestamp = new Date().getTime();
    let currentSaves = tryToParse(localStorage.getItem('guild'));
    let saves;
    if (Array.isArray(currentSaves)) {
      currentSaves = currentSaves?.length >= 3 ? currentSaves?.slice(1) : currentSaves;
      saves = [...(currentSaves || []), {
        timestamp,
        members: guild?.members
      }];
    } else {
      if (currentSaves) {
        saves = [currentSaves, {
          timestamp,
          members: guild?.members
        }];
      } else {
        saves = [{
          timestamp,
          members: guild?.members
        }];
      }
    }
    setDataTimestamp(saves);
    localStorage.setItem('guild', JSON.stringify(saves));
    setError('');
  };
  const exportToJson = async e => {
    setAnchorEl(e.currentTarget);
    try {
      const exportedData = {
        date: format(new Date(), 'dd/MM/yyyy HH:mm:ss'),
        members: guild?.members?.map(({
          name,
          gpEarned
        }) => ({
          name,
          gpEarned
        }))
      };
      await navigator.clipboard.writeText(JSON.stringify(exportedData, null, 2));
    } catch (e) {
      console.error('exportToJson -> ', e);
    }
  };
  const onClear = () => {
    localStorage.removeItem('guild');
    setDataTimestamp([]);
  };
  if (!guild || guild?.members?.length === 0) {
    return <Typography variant={'h3'} mb={3}>You have to be in a guild to view this page's content</Typography>;
  }
  return <>
    <NextSeo title="Guild | Idleon Toolbox" description="Keep track of your guild members, gp, bonuses and more" data-sentry-element="NextSeo" data-sentry-source-file="guild.jsx" />
    <Stack data-sentry-element="Stack" data-sentry-source-file="guild.jsx">
      <Stack direction={'row'} alignItems={'center'} gap={1} data-sentry-element="Stack" data-sentry-source-file="guild.jsx">
        <img src={`${prefix}data/G2icon${state?.account?.accountOptions?.[38]}.png`} alt={'guild-icon'} />
        <Typography variant={'h3'} data-sentry-element="Typography" data-sentry-source-file="guild.jsx">{state?.account?.accountOptions?.[37]} <Typography component="span" variant={'h5'} data-sentry-element="Typography" data-sentry-source-file="guild.jsx">({guild?.members?.length} / {guild?.maxMembers})</Typography></Typography>
      </Stack>
      <Stack sx={{
        mb: 1
      }} direction={'row'} gap={2} flexWrap={'wrap'} data-sentry-element="Stack" data-sentry-source-file="guild.jsx">
        <CardTitleAndValue title={'Members'} value={`${guild?.members?.length} / ${guild?.maxMembers}`} data-sentry-element="CardTitleAndValue" data-sentry-source-file="guild.jsx" />
        <CardTitleAndValue title={'Exp'} data-sentry-element="CardTitleAndValue" data-sentry-source-file="guild.jsx">
          <ProgressBar boxSx={{
            width: 200
          }} percent={guild?.totalGp / Math.round(guild?.levelReq) * 100} bgColor={'#f3dd4c'} data-sentry-element="ProgressBar" data-sentry-source-file="guild.jsx" />
          <Typography data-sentry-element="Typography" data-sentry-source-file="guild.jsx">{numberWithCommas(guild?.totalGp)} / {numberWithCommas(Math.round(guild?.levelReq))}</Typography>
        </CardTitleAndValue>
        <CardTitleAndValue title={'Saves (up to 3)'} data-sentry-element="CardTitleAndValue" data-sentry-source-file="guild.jsx">
          <Stack gap={1} data-sentry-element="Stack" data-sentry-source-file="guild.jsx">
            <Box data-sentry-element="Box" data-sentry-source-file="guild.jsx">
              {dataTimestamp?.reverse()?.map(({
                timestamp
              }, index) => {
                return <Typography key={timestamp + index}>#{index + 1} - {format(new Date(timestamp), 'dd/MM/yyyy HH:mm:ss')}</Typography>;
              })}
            </Box>
            <Stack direction={'row'} alignItems={'center'} gap={2} data-sentry-element="Stack" data-sentry-source-file="guild.jsx">
              <Button variant={'contained'} onClick={saveToLS} data-sentry-element="Button" data-sentry-source-file="guild.jsx">Save</Button>
              <Button variant={'contained'} onClick={exportToJson} data-sentry-element="Button" data-sentry-source-file="guild.jsx">Export</Button>
              <Popper anchorEl={anchorEl} handleClose={() => setAnchorEl(null)} data-sentry-element="Popper" data-sentry-source-file="guild.jsx" />
              <Button variant={'contained'} color={'warning'} onClick={onClear} data-sentry-element="Button" data-sentry-source-file="guild.jsx">Clear
                all</Button>
            </Stack>
            {error ? <Typography color={'error.light'}>{error}</Typography> : null}
          </Stack>
        </CardTitleAndValue>
      </Stack>
    </Stack>
    <Tabber tabs={['Members', 'Bonuses']} data-sentry-element="Tabber" data-sentry-source-file="guild.jsx">
      <GuildMembers members={guild?.members} saves={dataTimestamp} data-sentry-element="GuildMembers" data-sentry-source-file="guild.jsx" />
      <GuildBonuses bonuses={guild?.guildBonuses} data-sentry-element="GuildBonuses" data-sentry-source-file="guild.jsx" />
    </Tabber>
  </>;
};
const CardTitleAndValue = ({
  cardSx,
  title,
  value,
  children
}) => {
  return <Card sx={{
    my: {
      xs: 0,
      md: 3
    },
    width: 'fit-content',
    ...cardSx
  }} data-sentry-element="Card" data-sentry-component="CardTitleAndValue" data-sentry-source-file="guild.jsx">
    <CardContent data-sentry-element="CardContent" data-sentry-source-file="guild.jsx">
      <Typography sx={{
        fontSize: 14
      }} color="text.secondary" gutterBottom data-sentry-element="Typography" data-sentry-source-file="guild.jsx">{title}</Typography>
      {value ? <Typography>{value}</Typography> : children}
    </CardContent>
  </Card>;
};
export default Guild;